
html {
  margin: 0;
  padding: 0;
}
body {
  margin: 0 30px 0 30px;
  padding: 0;
  font-family: sans-serif;
  color: #1B997A;
  display: grid;
  grid-template-rows: 15% 75% 10%;
}
main {
  grid-row: 2/3;
  display: grid;
  grid-template-columns: 5% 42.5% 5% 42.5% 5%;
}
.side-bar {
  display: block;
}
main section {
  grid-column: 2/3;
}
main section h2 {
  margin: 0;
}
section {
  grid-column: 2/3;
}










header {
  grid-row: 1 / 2;
  display: grid;
  grid-template-columns: 25% 50% 15%;
  grid-template-rows: 50% 50%;
  padding-top: 10px;
}
img {
  grid-row: 1/3;
  grid-column: 1/2;
  height: 100px;
  width: auto;
}
header h1 {
  grid-row: 1/2;
  grid-column: 2/3;
  margin: 0;
  color: #C261CC;
  font-family: serif;
}

footer{
  position: absolute;
  bottom: "0";
  padding: "5px";
  margin: "0 auto";
  margin: 0 auto;
}
small{
  margin: 0 auto;
  color: black;
  font-size: .5rem;
  margin-left: 10px;
}

