}
h2{
  margin: 0;
  color: #61CCB1;
}
ul {
  list-style: none;
}
h4 {
  margin: 0;
}
small {
  color: black;
  font-size: .5rem;
  margin-left: 10px;
}
p {
  font-size: .8rem;
}

